import React,{Component} from 'react';
import { Tabs, Table, Row, Col, Button, Form, Select } from 'antd';import '../../index.css';

const { TabPane } = Tabs;
const { Option } = Select;
const columns = [
    {
      title: '',
      dataIndex: 'name',
      key: 'name',
    },
    {
      title: '',
      dataIndex: 'age',
      key: 'age',
    },
    { title: '', dataIndex: 'addr1ess', key: '1' },
    { title: '', dataIndex: 'addr2ess', key: '2' },
    { title: '', dataIndex: 'addre3ss', key: '3' },
    { title: '', dataIndex: 'addre4ss', key: '4' },
    { title: '', dataIndex: 'add5ress', key: '5' },
    { title: '', dataIndex: 'addr6ess', key: '6' }
  ];
  
  const data = [
    {
      key: '1',
      name: 'John Brown',
      age: 32,
      address: 'New York Park',
    },
    {
      key: '2',
      name: 'Jim Green',
      age: 40,
      address: 'London Park',
    },
  ];

class InoutLog extends Component {
 
    render() {
        return (
            <div className="mainBox inOutLog">
                <div className="mainCon">
                    <Tabs>
                    <TabPane tab="" key="1">
                        <Form className="ant-advanced-search-form">
                            <Row>
                                <Col>
                                    <Form.Item>
                                      <Select placeholder="">
                                        <Option value="chin14a"></Option>
                                        <Option value="usa2"></Option>
                                      </Select>
                                    </Form.Item>
                                </Col>
                                <Col>
                                    <Button className="searchBtn" type="primary" htmlType="submit">
                                        
                                        <span className="searchIcon"></span>
                                    </Button>
                                </Col>
                            </Row>
                            <Table columns={columns} dataSource={data} />
                        </Form>
                        </TabPane>
                    </Tabs>
                </div>
            </div>
        );
  }

  
}
export default InoutLog;