import React, { Component } from 'react';
import { Modal, Form, Input, Select } from 'antd';
import { connect } from 'react-redux';
import { isShow } from '../../store/actionCreators';
import './index.css'

const { Option } = Select;
class ModalRecharge extends Component {

  handleOk = e => {
    const {dispatch} = this.props;
    dispatch(isShow(false));
  };

  handleCancel = e => {
    const {dispatch} = this.props;
    dispatch(isShow(false));
  };

  render() {
    return (
        <Modal className="ModalBox modalRecharge"
          title=""
          visible={this.props.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          okText=""
          cancelText=""
        >
          <Form onSubmit={this.handleSubmit} className="login-form">
            <Form.Item label="">
              <Input placeholder="Username" />
            </Form.Item>
            <Form.Item label="">
              <Input/>
            </Form.Item>
            <Form.Item label="">
              <Input/>
            </Form.Item>
            <Form.Item label="">
              <Input />
            </Form.Item>
            <Form.Item label="">
              <Input />
            </Form.Item>
            <Form.Item label="">
              <Input />
            </Form.Item>
            <Form.Item label="">
              <Input />
            </Form.Item>
            <Form.Item label="">
              <Select placeholder="">
                <Option value="chin14a"></Option>
                <Option value="usa2"></Option>
              </Select>
            </Form.Item>
          </Form>
        </Modal>
    );
  }
}

const mapStateToProps = (state)=>{
    return {
        visible:state.visible
    }
};
const mapDispatchToProps = (dispatch)=>{
    return {
      dispatch
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(ModalRecharge);